import React, { Component,createRef } from 'react'

import { Card, Input, Button,DatePicker, Upload, message,Row,Col,Spin} from 'antd';
import { Form,Icon} from '@ant-design/compatible';
import '@ant-design/compatible/assets/index.css';
// import { LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import E from 'wangeditor'
import moment from 'moment'
// import  './sharePart.less'

import {getArticleById,saveArticle,uploadCapsule} from '../../../../services'

const formItemLayout = {
    labelCol:{
        span:3
    },
    wrapperCol:{
        span:18
    }
}
@Form.create()
class UploadArtifact extends Component {
    constructor(){
        super()
        // 获得  ref 标记的 虚拟DOM
        this.editorRef=createRef()
        this.state={
            isLoading:false,
            dataSource:{}
        }
    }
    componentDidMount(){
        this.initEditor()
        this.setState({
            isLoading:true,
            loading:false,
        })

        // this.fillFormData(this.props.dataSource)
        // getArticleById(this.props.match.params.id)
        // .then(resp =>{
        //     const {id, ...data} = resp
        //     data.capsule_time = moment(data.capsule_time)
        //     this.props.form.setFieldsValue(data)
        //     this.editor.txt.html(data.content)
        // })
        // .finally(()=>{
        //     this.setState({
        //         isLoading:false
        //     })
        // })
    }

    handleSubmit = (e)=>{
        e.preventDefault()
        this.setState({
            isLoading:true
        })
        this.props.form.validateFields((err, values) => {
            if(!err){
                console.log(values)
            }
        })
    }
    
    initEditor = () => {
        this.editor = new E(this.editorRef.current)
        //因为  this.editorRef  是虚拟DOM，需要使用  current 
        this.editor.customConfig.onchange = (html) => {  
            // 这里是  wangeditor 带的方法，每当 编辑器内容有所改变便会触发。  参数 html ，是 html式 的文本内容
            this.props.form.setFieldsValue({
                //   setFieldsValue,  对   {getFieldDecorator('content',....   这里定义的  'content' 进行赋值
                artifact_info: html
            })
        }
        this.editor.create()
    }

    initUpload = ()=>{
        
    }
     getBase64 = (img, callback)=> {
        const reader = new FileReader();
        reader.addEventListener('load', () => callback(reader.result));
        reader.readAsDataURL(img);
      }

   beforeUpload = (file)=> {
        const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
        if (!isJpgOrPng) {
          message.error('You can only upload JPG/PNG file!');
        }
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
          message.error('Image must smaller than 2MB!');
        }
        return isJpgOrPng && isLt2M;
      }

      handleChange = info => {
          console.log('info: ',info)
          this.props.form.setFieldsValue({
            artifact_img: info
        })
        if (info.file.status === 'uploading') {
          this.setState({ loading: true });
          return;
        }
        if (info.file.status === 'done') {
          this.getBase64(info.file.originFileObj, imageUrl =>
            this.setState({
              imageUrl,
              loading: false,
            }),
          );
        }
      };
      
    onClickBack=()=>{
        this.props.history.push('/admin/info/artifact')
    }

    render() {
        const{
            getFieldDecorator
        }=this.props.form
        const uploadButton = (
            <div>
              {/* {this.state.loading ? <LoadingOutlined /> : <PlusOutlined />} */}
              <div className="ant-upload-text">Upload</div>
            </div>
        )
        const { imageUrl } = this.state;
        return (
            <Card  
            title={"时讯"}
            bordered={false} 
            extra={<Button onClick={this.onClickBack}> 取消 </Button>} 
        >
            <Form 
                onSubmit={this.handleSubmit} 
                className="login-form"
                {...formItemLayout}
            >
                <Form.Item
                    label="标题"
                >
                {getFieldDecorator('artifact_title', {
                    rules: [{ required: true, message: '标题是必填项' }],
                })(
                    <Input
                        placeholder="标题"
                    />,
                )}
                </Form.Item>
                <Form.Item
                    label="名称"
                >
                {getFieldDecorator('artifact_name', {
                    rules: [{ required: true, message: '名称是必填项' }],
                })(
                    <Input
                        placeholder="标题"
                    />,
                )}
                </Form.Item>
                <Form.Item
                    label="作品链接"
                >
                    {
                        getFieldDecorator('artifact_url',{
                            rules:[
                                {required:true,message:'作品链接是必填项'}
                            ]
                        })
                        ( <Input placeholder='填写链接' />)
                    }
                </Form.Item>
                {/* <Form.Item
                    label="创建时间"
                >
                {getFieldDecorator('sharePart_time', {
                    rules: [
                        { required: true, message: '时间是必填项' },
                    ],
                })(
                    <DatePicker showTime placeholder="Select Time"  />
                )}
                </Form.Item> */}
                {/* <Form.Item
                    label="作品图片"
                >
                {getFieldDecorator('artifact_img', {
                    rules: [
                        { required: false, message: '图片是必填项' },
                    ],
                })(
                    <DatePicker showTime placeholder="Select Time"  />
                )}
                </Form.Item> */}
                <Form.Item
                    label="作品图片" 
                >
                    
                {getFieldDecorator('artifact_img', {
                    rules: [
                        { required: false, message: '图片是必填项' },
                    ],
                })(
                    <>
                        <Upload
                        name="avatar"
                        listType="picture-card"
                        className="avatar-uploader"
                        showUploadList={false}
                        action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                        beforeUpload={this.beforeUpload}
                        // type="file" 
                        // className="file"
                        onChange={this.handleChange}
                        >
                            <Spin spinning={this.state.loading}>
                                {imageUrl ? <img src={imageUrl} alt="avatar" style={{ width: '100%' }} /> : uploadButton}
                            </Spin>
                        </Upload>
                    </>
                    
                    
                )}
                   

                </Form.Item>
                <Form.Item
                        label="作品信息"
                    >
                    {getFieldDecorator('artifact_info', {
                        rules: [
                            { required: true, message: '信息是必填项' },
                        ],
                    })(
                        //  如何获得一个虚拟 DOM。如下
                        //  使用  ref 属性标记一个虚拟 DOM，如： <div ref={this.editorRef}> </div>标记为 this.editorRef
                    <div className='my-editor' ref={this.editorRef} >
                        
                    </div>
                    )}
                </Form.Item>
                <Row>
                    <Col span={2} offset={3}>
                        <Button   type="primary" htmlType="submit" className="login-form-button">
                            确定
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Card>
        )
    }
}
export default  UploadArtifact
